.header{
    height: 60px;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 100;
    background-color: #131921;
  }

  .header__search{
      display: flex;
      flex: 1;
      align-items: center;
      border-radius: 24px;
  }

  .header__searchInput{
      height: 12px;
      padding: 10px;
      border: none;
      width: 100%;
  }

  .header__searchIcon{
      padding: 5px;
      height: 22px !important;
      background-color: #cd9042;
  }

.header__logo{
    width: 100px;
    object-fit: contain;
    margin: 0 20px;
    margin-top: 18px;
}

.header__optionFirstLine{
    font-size: 10px;
}

.header__optionSecondLine{
    font-size: 13px;
    font-weight: 800;
}


.header__optionCart{
    display: flex;
    align-items: center;
    color: #fff;
}

.header__CartCount{
    margin-left: 10px;
    margin-right: 10px;
}
.header__nav{
    display: flex;
    justify-content: space-between;
}


.header__option{
    display: flex;
    flex-direction: column;
    margin-right: 10px;
    margin-left: 10px;
    color: #fff;
}

